
  import {setTitle} from '@/common/js/common';
  import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
;

  export default{
    data(){
      return{
        user_name:"",
        user_tel:"",
        user_code:"",
        count:"",
        token:[],
        issend:true,
        nums:"",
        clock:"",
        idtype:[],
        housedesc:[],
        typelist:[["","",""]],
        list:[],
        validata:"",
        estate:[],
        e_type:[],
        outer_list:[],
        this_vid:"",
        isLoading:false,
        dialog_title:"",
        content:"",
        confirm_show:false
//        id_code:""
      }
    },
    components:{
      Loading,XInput,XButton,Group,PopupPicker,Alert
    },
    mounted(){
        setTitle("");
        let self = this;
        if(service_url){
          this.$http.get(service_url+"/o2o/common/wx/getVillage").then((data)=>{
            self.estate=data.body.data;
            let name_list=[];
            let outer_list=[];
            for(let i =0;i<self.estate.length;i++){
              name_list.push(self.estate[i].name);
            }
            outer_list.push(name_list);
            this.outer_list=outer_list;
          });
        }else{
          this.$http.get("/api/getVillage").then((data)=>{
              self.estate = data.body.data.data;
              let name_list=[];
              let outer_list=[];
              for(let i =0;i<self.estate.length;i++){
                name_list.push(self.estate[i].name);
              }
              outer_list.push(name_list);
            this.outer_list=outer_list;
          })
        }
      document.getElementById("index_loading").style.display="none";
    },
    methods:{
      onChange_e(type){
        let this_id;
        this.e_type=type;
        for(let i =0;i<this.estate.length;i++){
            if(type[0]===this.estate[i].name){
              this_id=this.estate[i].id
            }
        }
        this.this_vid=this_id;
        let self = this;
        this.$http.get(service_url+"/o2o/common/wx/houselist?vid="+this_id).then((data)=>{
          self.list=data.body.data;
        });
        if(service_url){
          this.$http.get(service_url+"/o2o/common/wx/houselist?vid="+this_id).then((data)=>{
            self.list=data.body.data;
          });
        }else{
          console.log(1);
        }
      },
      onChange_u(type){
        this.idtype=type;
      },
      onChange_h(type){
        this.housedesc=type;
      },
      doLoop()
      {
        this.nums--;
        if(this.nums >= 0){
          this.count=this.nums+'';
        }else{
          clearInterval(this.clock); //js
          this.issend=true;
          this.count="";
          this.validata="";
          this.nums = 60; //
        }
      },
      register(){

        /*if(JSON.stringify(this.token)===JSON.stringify(this.user_code)){
            const items ={
            data: {
              name:this.user_name,
                phone:this.user_tel,
//                idcode:this.id_code, //
                idtype:this.idtype, //, ,,
                housedesc:this.housedesc //housedesc
            }
          };
            let self = this;
            this.$http.post(service_url+"/o2o/common/wx/reg/request",items).then((data)=>{
              if(data.body.statusCode==="ok"){
                self.$router.push({
                  path:'index'
                })
              }else{
                  alert(data.body.message);
              }
            })
        }*/
        let self = this;
        if(!this.user_name||this.user_name===""){
          this.dialog_title="";
          this.content="";
          this.confirm_show=true;
          return;
        }
        if(!this.user_tel||this.user_tel===""){
          this.dialog_title="";
          this.content="";
          this.confirm_show=true;
          return;
        }
        if(JSON.stringify(this.token)===JSON.stringify(this.user_code)){
          if(this.idtype.length===0){
            this.dialog_title="";
            this.content="";
            this.confirm_show=true;
            return;
          }
          if(this.e_type.length===0){
            this.dialog_title="";
            this.content="";
            this.confirm_show=true;
            return;
          }
          if(this.housedesc.length===0){
            this.dialog_title="";
            this.content="";
            this.confirm_show=true;
            return;
          }
          this.isLoading=true;
          const items ={
            data: {
              name:this.user_name,
              phone:this.user_tel,
//                idcode:this.id_code, //
              idtype:this.idtype, //, ,,
              housedesc:this.housedesc, //housedesc
              vid:this.this_vid
            }
          };
          this.$http.post(service_url+"/o2o/common/wx/reg/request",items).then((data)=>{
            this.isLoading=false;
            if(data.body.statusCode==="ok"){
              this.dialog_title="";
              this.content="";
              this.confirm_show=true;
              userProfile=data.body.userProfile;
              self.$router.push({
                path:'index'
              })
            }else{
              this.dialog_title="";
              this.content=data.body.message;
              this.confirm_show=true;
            }
          })
        }else{
          this.dialog_title="";
          this.content="";
          this.confirm_show=true;
        }


      },
      getCode(){
          let myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
          if(this.user_tel&&this.user_tel!==""&&this.user_tel.length===11&&myreg.test(this.user_tel)){
            let self = this;
             this.nums =60;
             this.issend=false;
             this.$http.get(service_url+"/o2o/common/wx/gettoken?phone="+this.user_tel).then((data)=>{
             self.token=data.body.token;
             });
             this.count=this.nums+'';
             this.clock = setInterval(this.doLoop, 1000);
          }else{
              this.dialog_title="";
              this.content="";
              this.confirm_show=true;
          }

      }
    }
  }



